<template>
  <div id="viewContent">
    <Store
      :player="player"
      @buyKey="buyKey($event[0], $event[1])"
      :keys="keys"
      @goBack="goBack"
      v-if="selectedWindow == 'store'"
    />
    <OpenChests
      :rarity="rarity"
      :keys="keys"
      @openKey="openKey($event[0], $event[1])"
      :player="player"
      @goBack="goBack"
      v-if="selectedWindow == 'openChests'"
      :lastOpenedOption="lastOpenedOption"
      @lastOpened="lastOpened"
    />
    <Inventory
      :player="player"
      :items="items"
      :rarity="rarity"
      @goBack="goBack"
      @lockItem="lockItem($event)"
      @unlockItem="unlockItem($event)"
      @sellItems="sellItems($event[0], $event[1])"
      @sellInventory="sellInventory"
      v-if="selectedWindow == 'inventory'"
    />
    <Character @deletePlayer="deletePlayer" :prestigeRequiredLevel="prestigeRequiredLevel" @prestigeLevelUp="prestigeLevelUp" :player="player" @goBack="goBack" v-if="selectedWindow == 'character'" />
    <Quests @goBack="goBack" v-if="selectedWindow == 'quests'" />
    <Achievements @goBack="goBack" v-if="selectedWindow == 'achievements'" />
    <Settings @goBack="goBack" v-if="selectedWindow == 'settings'" />
    <Menu @openMenu="openMenu($event)" v-if="selectedWindow == 'menu'" />
  </div>
</template>

<script>
import Store from "./Store.vue";
import Inventory from "./Inventory.vue";
import OpenChests from "./OpenChests.vue";
import Character from "./Character.vue";
import Achievements from "./Achievements.vue";
import Settings from "./Settings.vue";
import Quests from "./Quests.vue";

import Menu from "./Menu.vue";
export default {
  name: "GameContent",
  props: ["player", "items", "keys", "rarity", "prestigeRequiredLevel"],
  components: {
    Store,
    Inventory,
    OpenChests,
    Character,
    Menu,
    Achievements,
    Settings,
    Quests,
  },
  data() {
    return {
      selectedWindow: "menu",
      lastOpenedOption: "bronze"
    };
  },
  methods: {
    lastOpened(keyName) {
        this.lastOpenedOption = keyName;
    },
    openMenu(menuOption) {
        this.selectedWindow = menuOption;
    },
    goBack() {
        this.selectedWindow = "menu";
    },
    buyKey(keyName, amount) {
        this.$emit("buyKey", [keyName, amount]);
    },
    openKey(keyName, amount) {
        this.$emit("openKey", [keyName, amount]);
    },
    lockItem(index) {
        this.$emit("lockItem", index);
    },
    unlockItem(index) {
        this.$emit("unlockItem", index);
    },
    sellItems(index, amount) {
        this.$emit("sellItems", [index, amount]);
    },
    sellInventory(){
      this.$emit('sellInventory')
    },
    prestigeLevelUp(){
      this.$emit('prestigeLevelUp');
    },
    deletePlayer(){
      this.$emit('deletePlayer');
    },
  },
};
</script>

<style scoped>
.web #viewContent {
  height: calc(100% - 4.4rem);
  width: 100%;
  margin: 0 auto;
  max-width: 36rem;
  padding: 0 1rem;
}

.electron #viewContent{
    height: calc(100vh - 6.2rem);
    width: 100%;
    margin: 0 auto;
    max-width: 36rem;
    padding: 0 1rem;
}
</style>