<template>
  <div class="container">
    <BackButton pageTitle="Open Chests" @goBack="goBack" />
    <div class="content">
        <div class="tabs">
            <div class="tab" v-bind:class="[isActivated(item.name)]" @click="selectMenu(item.name)" v-for="item in keys" :key="item.name"> {{ item.name }} Chest</div>
        </div>
        <div class="chestUI" v-for="item in keys" :key="item.name">
          <div class="chest flex" v-if="this.showedChest == item.name">
              <div class="flex flex-col">
                  <div class="flex">
                    <div class="img_container self-center">
                    <img src="../assets/images/chest.png" alt="">
                  </div>
                  <div class="name self-center">{{ item.name }} Chest</div>
                  <div class="ml-auto button-grid">
                    <button :disabled="this.player.keys[item.name] < 1" @click="openKey(item.name, 1)">1</button>
                    <button :disabled="this.player.keys[item.name] < 5" @click="openKey(item.name, 5)">5</button>
                    <button :disabled="this.player.keys[item.name] < 10" @click="openKey(item.name, 10)">10</button>
                    <button :disabled="this.player.keys[item.name] < 25" @click="openKey(item.name, 25)">25</button>
                    <button :disabled="this.player.keys[item.name] < 50" @click="openKey(item.name, 50)">50</button>
                  </div>
                </div>
                <div class="title">Chances:</div>
                <div>
                  <div class="chances">
                    <div class="chance" :style="{ color: this.rarity[key_item.name].color }" v-for="key_item in item.chances" :key="key_item.name">
                      <span v-if="this.player.prestige_level > item.requiredPrestigeLevel">{{ key_item.chance }}%</span>
                    </div>
                  </div>
                  <div v-if="this.player.prestige_level < item.requiredPrestigeLevel" class="warning">You have to reach prestige level 5 to unlock more information.</div>
                </div>
              </div>
          </div>
        </div>
        <div class="" v-if="showLoot">
          <div class="cat-title">Dropped:</div>
          <div class="new-items">
            <div class="drop">
              <div v-for="item in player.newest_items" :key="item.unique_id" class="new-item">
                <span :style="{color: rarity[item.Rarity].color }" @click="itemInfo(item)">{{ item.Name }}</span>
              </div>
            </div>
            <div class="itemInfo">
              <ItemInfo @closeItemInfo="closeItemInfo()" :rarity="rarity" v-if="showItem" :item="item"  />
            </div>
        </div>
        </div>
    </div>
  </div>
</template>

<script>
import BackButton from "./BackButton.vue";
import ItemInfo from "./ItemInfo.vue";

export default {
  name: "OpenChests",
  components: {
    BackButton,
    ItemInfo
  },
  props: ["player", "rarity", "keys", "lastOpenedOption"],
  data(){
    return {
      showedChest: "bronze",
      showLoot: false,
      showItem: false,
      item: {}
    }
  },
  methods: {
    selectMenu(keyName){
      this.showedChest = keyName;
      this.$emit('lastOpened', keyName);
    },
    isActivated(keyName){
      if(keyName == this.showedChest) return "active";
    },
    goBack() {
      this.$emit("goBack");
    },
    openKey(keyName, amount) {
      this.item = {}
      this.showItem = false;
      this.$emit("openKey", [keyName, amount]);
      this.showLoot = true;
      console.log(this.player.newest_items)
    },
    itemInfo(item){
      if(this.item == item){
        this.item = {}
        return this.showItem = false;
      }
      this.item = item;
      this.showItem = true;
      console.log(item);
    },
    closeItemInfo(){
      this.showItem = false;
    }
  },
  created(){
    this.showedChest = this.lastOpenedOption;
    console.log(this.rarity);
  }
};
</script>

<style scoped>
.container {
  height: 100%;
}
.content {
  height: calc(100% - 3rem);
  width: 100%;
}
.warning{
    text-align: center;
    margin-top: 1rem;
    color: yellow;
}
.flex{
  display: flex;
}
.ml-auto{
  margin-left: auto;
}
.flex-col{
  flex-direction: column;
}
.self-center{
  align-self: center;
}

.tabs{
  display: flex;

  text-align: center;
  font-weight: 500;
  gap: 0.4rem;
  justify-items: flex-start;
}

.title{
  font-size: 0.9rem;
  margin-top: 1rem;
  font-weight: 500;
}

.chances{
  display: grid;
  grid-template-columns: repeat(8, minmax(0, 1fr));
  margin-top: 0.5rem;
  font-size: 0.8rem;
  text-align: left;
  padding: 0.5rem;
  border: 1px solid gray;
  border-radius: 0.2rem;
  background: #1e1e1e;
  font-weight: 500;
}

.tab{
  padding: 0.5rem;
  background: #111111;
  border: 2px solid transparent;
  color: white;
  border-radius: 0.2rem;
  font-size: 0.9rem;
}

.tab::first-letter{
  text-transform: uppercase;
}

.tab:hover{
  filter: brightness(70%);
  cursor: pointer;
}
.tab.active{
  border-color: rgb(35, 97, 190);
  pointer-events: none;
}

.button-grid{
    display: grid;
    grid-template-columns: repeat(5, minmax(0, 1fr));
    margin-top: 0.5rem;
    column-gap: 0.5rem;
}

.button-grid button{
    padding: .5rem;
    border: 2px solid rgb(58, 160, 72);
    border-radius: 0.2rem;
    background: rgb(43, 43, 43);
    color: var(--text);
    cursor: pointer;
}

.button-grid button:hover{
    background: rgb(26, 26, 26);
}

.button-grid button:disabled{
  border-color: transparent;
  pointer-events: none;
  background: rgb(104, 104, 104);
}

.cat-title{
  color: var(--text);
  font-weight: 600;
  font-size: 1.1rem;
  margin-top: 1rem;
  margin-bottom: 0.8rem;
}
.new-items{
    width: 100%;
    height: calc( 100vh - 25rem);
    overflow-y: auto;
    font-size: 0.9rem;
}

.new-items span{
      cursor: pointer;
}

.drop{
  font-size: 0.9rem;
}

.itemInfo{
  width: 100%;
  position: fixed;
  bottom: 7rem;
  left: 0;
}

.item_info{
    width: 80%;
    margin: 0 auto;
}

.drop{
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: repeat(20, minmax(0, 1fr));
}

.new-item{
  margin-top: 0.2rem;
  font-weight: 400;
  width: 16rem;
   white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


.chest{
  padding: 1rem;
  background: #181818;
  color: var(--text);
  width: 100%;
  margin-top: 0.5rem;
  border-radius: 0.2rem;
}

.chest .name::first-letter{
  text-transform: uppercase;
}

.chest > .flex{
  width: 100%;
}



.top{
  display: grid;
  grid-template-columns: auto 8rem;
}

.name{
  margin-left: 1rem;
  font-weight: 500;
}

.img_container{
    height: 2rem;
    width: 2rem;
}

.item .img_container img{
    height: 100%;
}


@media only screen and (max-width: 550px) {
  .drop{
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}
</style>