<template>
    <div class="header">
        <div class="bar">
            <div class="icon_container self-center flex">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 self-center" viewBox="0 0 20 20" fill="currentColor">
                    <path fill-rule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-6-3a2 2 0 11-4 0 2 2 0 014 0zm-2 4a5 5 0 00-4.546 2.916A5.986 5.986 0 0010 16a5.986 5.986 0 004.546-2.084A5 5 0 0010 11z" clip-rule="evenodd" />
                </svg>
                <div class="self-center" id="level">{{ this.player.level }}</div>
            </div>
            <div class="icon_container self-center flex">
                <img class="self-center" src="../assets/images/gold.png" alt="">
                <div class="self-center" id="gold">{{ abbreviateNumber(this.player.gold) }}</div>
            </div>
            <div class="keys">
                <div class="self-center flex" v-for="item in keys" :key="item.name">
                    <div class="icon_container" v-if="this.player.prestige_level > item.requiredPrestigeLevel">
                        <img class="self-center" v-bind:src="require('../assets/images/' + item.name + 'Key.png')" alt="">
                        <div class="self-center" id="gold">{{ this.player.keys[item.name] }}</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="progress-container">
            <div class="tooltip">Experience {{ experience }}</div>
            <div class="progress-bar">
                <div id="experience" class="progress" v-bind:style="{ width: this.progress}"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Header",
    props: ["player", "progress", "experience", "keys"],
    created: function () {
        console.log(this.keys)
    },
    methods: {
        abbreviateNumber(num) {
            var fixed = 0;
        if (num === null) { return null; } // terminate early
        if (num === 0) { return '0'; } // terminate early
        fixed = (!fixed || fixed < 0) ? 0 : fixed; // number of decimal places to show
        var b = (num).toPrecision(2).split("e"), // get power
            k = b.length === 1 ? 0 : Math.floor(Math.min(b[1].slice(1), 14) / 3), // floor at decimals, ceiling at trillions
            c = k < 1 ? num.toFixed(0 + fixed) : (num / Math.pow(10, k * 3) ).toFixed(1 + fixed), // divide by power
            d = c < 0 ? c : Math.abs(c), // enforce -0 is 0
            e = d + ['', 'K', 'M', 'B', 'T'][k]; // append power
        return e;
        }
    }
}

</script>

<style scoped>
.self-center{
  align-self: center;
}
.header{
    background: var(--header);
    height: 4.4rem;
}
#level{
    margin-left: 0.4rem;
    font-size: 0.9rem;
}

#gold{
    font-size: 0.9rem;
    margin-left: 0.2rem;
}
.flex{
    display: flex;
}
.header .bar{
    padding: 0.5rem;
    width: 100%;
    display: grid;
    grid-template-columns: 6rem 5rem auto;
    color: var(--text);
    height: 3.6rem;
}

.keys{
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    height: 1.6rem;
    font-size: 0.8rem;
    margin-left: auto;
    width: 14rem;
}

.progress-container{
    width: 100%;
    padding: 0.2rem;
    position: relative;
}

.progress-container .tooltip{
    padding: 0.5rem 0.8rem;
    font-size: 0.9rem;
    position: absolute;
    right: 0.5rem;
    top: 1rem;
    border-radius: 0.2rem;
    background-color: rgb(10, 10, 10);
    color: white;
    opacity: 0;
    pointer-events: none;
    transition: opacity 250ms ease-in;
}

.progress-container:hover .tooltip{
    opacity: 1;
}

.tooltip:before {
       content: "";
       display: block;
       position: absolute;
       right: 0.1rem;
       top: -4px;
       border-bottom: 6px solid rgb(10, 10, 10);
       border-left: 6px solid transparent;
       border-right: 6px solid transparent;
  }

.progress-bar{
    height: 0.3rem;
    background: #979797;
}

.progress{
    background: rgb(33, 177, 88);
    height: 100%;
}

.icon_container{
    height: 1.6rem;
}
.icon_container svg{
    height:100%;
}
.icon_container img{
    height:100%;
}

.keys .icon_container{
    height: 1.4rem;
}

</style>