<template>
    <div class="container">
        <BackButton pageTitle="Shop" @goBack="goBack" />
        <div class="content">
            <div class="" v-for="item in keys" :key="item.price">
                <div v-if="this.player.prestige_level > item.requiredPrestigeLevel" class="item">
                    <img class="self-center" :src="require('../assets/images/' + item.name + 'Key.png')"  alt="">
                    <div class="title self-center">
                        <div class="">{{ item.nickname }}</div>
                        <div class="prices">{{ item.price }} / {{ item.price * 5 }} / {{ item.price * 25 }}</div>
                    </div>
                    <div class="ml-auto self-center">
                        <div class="grid">
                            <button :disabled="this.player.level < item.requiredLevel || this.player.gold < (item.price * 1)" class="" @click="buyKey(item.name, 1)">+1</button>
                            <button :disabled="this.player.level < item.requiredLevel || this.player.gold < (item.price * 5)" class="" @click="buyKey(item.name, 5)">+5</button>
                            <button :disabled="this.player.level < item.requiredLevel || this.player.gold < (item.price * 25)" class="" @click="buyKey(item.name, 25)">+25</button>
                        </div>
                        <div v-if="this.player.level < item.requiredLevel" class="info">You have to reach level {{ item.requiredLevel }}</div>
                    </div>
                </div>
                <div v-if="this.player.prestige_level < item.requiredPrestigeLevel" class="warning">You have to reach prestige level {{ item.requiredPrestigeLevel }} to unlock a new key.</div>
            </div>
        </div>
    </div>  
</template>

<script>

    import BackButton from "./BackButton.vue";


    export default {
        name: "Store",
        props: ["keys", "player"],
        components: {
            BackButton
        },
        methods:{
            goBack(){
                this.$emit('goBack')
            },
            buyKey(keyName, amount){
                this.$emit('buyKey', [keyName, amount]);
            }
        },
        created(){
            console.log(this.keys);
        }
    }
</script>

<style scoped>
.info{
    font-size: 0.7rem;
    color: var(--text);
    margin-top: 0.2rem;
}
.prices{
    font-size: 0.7rem;
}
.container{
    height: 100%;
}
.content{
    height: calc(100% - 3rem);
    width: 100%;
    padding: 2rem;
}

.item{
    display: flex;
    padding: 1rem;
    width: 100%;
    background: #181818;
    border-radius: 0.2rem;
    margin-top: 0.4rem;
}
.grid{
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
}

.title{
    font-weight: 500;
    color: var(--text);
    margin-left: 1rem;
}

.warning{
    text-align: center;
    margin-top: 1rem;
    color: yellow;
}
.self-center{
    align-self: center;
}
.ml-auto{
    margin-left: auto;
}

button{
    padding: 0.5rem 0.5rem;
    border-radius: 0.2rem;
    margin-right: 0.5rem;
    cursor: pointer;
    color: var(--text);
    background: rgb(43, 43, 43);
    border: 2px solid rgb(58, 160, 72);
}
button:hover{
    background: rgb(26, 26, 26);
    font-weight: 500;
}

button:disabled{
    pointer-events: none;
    background: rgb(104, 104, 104);
    border-color: transparent;
}
</style>