<template>
    <div class="modTools">
        <div class="container">
            <div class="title">Mod Tools</div>
            <button @click="deletePlayer">Delete Account</button>
            <button @click="playerAddLevel">Add Level</button>
            <button @click="playerAddPrestige">Add Prestige</button>
            <button @click="playerAddGold">Add gold</button>
            <button @click="addKeys('bronze', 500)">Add Bronze Key</button>
            <button @click="addKeys('silver', 500)">Add Silver Key</button>
            <button @click="addKeys('gold', 500)">Add Gold Key</button>
            <button @click="addKeys('platinum', 500)">Add Platinum Key</button>
            <div class="title">Active Buttons</div>
            <button @click="openKey('bronze', 1)">Open Bronze Key</button>
            <button @click="openKey('silver', 1)">Open Silver Key</button>
            <button @click="openKey('gold', 1)">Open Gold Key</button>
            <button @click="openKey('bronze', 5)">Open 5 Bronze Keys</button>
            <button @click="openKey('silver', 5)">Open 5 Silver Keys</button>
            <button @click="openKey('gold', 5)">Open 5 Gold Keys</button>
            <div class="title">Inventory</div>
            <button @click="viewWholeInventory()">View all</button>
            <button @click="sellInventory()">Sell All</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ModTools",
        props: ["player"],
        created: function () {
            console.log("Mod Tools loaded.")
        },
        methods: {
            deletePlayer(){
                this.$emit('deletePlayer');
            },
            playerAddLevel(){
                this.$emit('playerAddLevel');
            },
            playerAddPrestige(){
                this.$emit('playerAddPrestige');
            },
            playerAddGold(){
                this.$emit('playerAddGold');
            },
            addKeys(keyName, amount){
                this.$emit('addKeys', [keyName, amount]);
            },
            buyKey(keyName, amount){
                this.$emit('buyKey', [keyName, amount]);
            },
            openKey(keyName, amount){
                this.$emit('openKey', [keyName, amount]);
            },
            viewWholeInventory(){
                this.$emit('viewWholeInventory')
            },
            sellInventory(){
                this.$emit('sellInventory')
            }
        }
    }

</script>

<style scoped>
.modTools{
    position: fixed;
    top: 9rem;
    right: 0;
    text-align: center;
    user-select: none;
    z-index: 50;
}
.container{
    padding: 1rem;
    background: var(--header);
    border-top-left-radius: 0.2rem;
    border-bottom-left-radius: 0.2rem;
    display: flex;
    flex-direction: column;
}
.title{
    font-size: 0.9rem;
    color: var(--text)
}

button{
    margin-top: 0.4rem;
    outline: none;
    border: none;
    border-radius: 0.2rem;
    padding: 0.2rem 0.4rem;
    background: rgb(194, 42, 42);
    color: white;
    cursor: pointer;
}

button:hover{
    background: rgb(163, 33, 33);
}
</style>