<template>
    <div class="item_info">
        <div class="header flex">
            <div class="flex self-center">
                <div :style="{color: rarity[item.Rarity].color }" class="name self-center">{{ item.Name }}</div>
                [<div class="id self-center">{{ item.ItemID }}</div>]
            </div>
            <div class="ml-auto self-center">
                <button @click="closeItemInfo">
                    <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                        <path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd" />
                    </svg>
                </button>
            </div>
        </div>
        <div class="body">
            <div class="sprite_container">
                <img v-if="item.Type != 'Unique'" :src="`https://web.simple-mmo.com${item.image}`" alt="Server down">
                <img v-if="item.Type == 'Unique'" :src="require('../assets/images/' + item.image)" alt="Created item">
            </div>
            <div class="info_container">
                <div class="">
                    <div class="level">Level {{ item.Level }} {{ item.Type }}</div>
                    <div :style="{color: rarity[item.Rarity].color }" class="rarity">{{ item.Rarity }}</div>
                </div>
                <div class="">
                    <div v-if="item.Stat1 != '' && item.Stat1 != 'none'">{{item['Stat1-Value']}} {{fixStats(item.Stat1)}} </div>
                    <div v-if="item.Stat2 != '' && item.Stat2 != 'none'">{{item['Stat2-Value']}} {{fixStats(item.Stat2)}} </div>
                    <div v-if="item.Stat3 != '' && item.Stat3 != 'none'">{{item['Stat3-Value']}} {{fixStats(item.Stat3)}} </div>
                </div>
            </div>
            <div class="action_container"></div>
        </div>
        <div class="footer"></div>
    </div>
</template>

<script>
    export default {
        name: "ItemInfo",
        props: ["item", "rarity"],
        methods:{
            fixStats(stat){
                if(stat == "str") return "Strength";
                if(stat == "def") return "Defence";
                if(stat == "dex") return "Dexterity";
                if(stat == "energy") return "Energy";
                if(stat == "health") return "Health";
                return stat;
            },
            closeItemInfo(){
                this.$emit('closeItemInfo');
            }
        },
        created(){
            console.log(this.item);
        }
    }
</script>

<style scoped>
.info_container{
    margin-left: 0.5rem;
    display: grid;
    grid-template-columns: auto 8rem;
    column-gap: 0.5rem;
    width: 80%;
}
.sprite_container{
    width: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sprite_container img{
    height: auto;
    max-width: 32px;
}

.flex{
    display: flex;
}

.body{
    margin-top: 1rem;
    display: flex;
}
.item_info{
    color: var(--text);
    padding: 1rem;
    background: #141414;
    border-radius: 0.5rem;
    font-size: 0.9rem;
}
.name{
    user-select: all;
    font-weight: 500;
    margin-right: 1rem;
}
.id{
    user-select: all;
}
.level{
    display: flex;
}

.ml-auto{
    margin-left: auto;
}

.self-center{
    align-self: center;
}

button{
    cursor: pointer;
    outline: none;
    border: none;
    padding: 0.2rem;
    border-radius: 0.2rem;
    height: 1.5rem;
    align-self: center;
    font-weight: 500;
}

button svg{
    height: 100%;
}

button:hover{
    background: rgb(202, 202, 202); 
}
</style>