<template>
  <div class="container">
    <Login
      @createPlayer="createPlayer($event[0], $event[1])"
      v-if="newPlayer"
    />
    <ItemsList @loadItems="loadItems($event)" />
    <BlackList @loadBlackList="loadBlackList($event)" />
    <EventList @loadEventList="loadEventList($event)" />
    <div v-if="!newPlayer" id="gameWindow">
      <Header
        :progress="getExperiencePercentage()"
        :player="player"
        :keys="keys"
        :experience="getExperienceStatus()"
      />
      <ModTools
        @deletePlayer="deletePlayer"
        @playerAddLevel="playerAddLevel"
        @playerAddPrestige="playerAddPrestige"
        @playerAddGold="playerAddGold"
        @addKeys="addKeys($event[0], $event[1])"
        @buyKey="buyKey($event[0], $event[1])"
        @openKey="rollItems($event[0], $event[1])"
        @viewWholeInventory="viewWholeInventory"
        @sellInventory="sellInventory"
        :player="player"
        v-if="development"
      />
      <GameContent 
      @openKey="rollItems($event[0], $event[1])"
      :keys="keys" 
      :items="items" 
      :player="player" 
      :rarity="rarity"
      @buyKey="buyKey($event[0], $event[1])"
      @lockItem="lockItem($event)"
      @unlockItem="unlockItem($event)"
      @sellItems="sellItems($event[0], $event[1])"
      @sellInventory="sellInventory"
      @prestigeLevelUp="prestigeLevelUp"
      :prestigeRequiredLevel="getPrestigeLevelRequired()"
      @deletePlayer="deletePlayer"
      />
    </div>
  </div>
</template>

<script>
import Header from "./Header.vue";
import Login from "./Login.vue";
import ModTools from "./ModTools.vue";
import ItemsList from "./ItemsList.vue";
import GameContent from "./GameContent.vue";
import EventList from "./EventList.vue";
import BlackList from "./BlackList.vue";

export default {
  name: "GameContainer",
  components: {
    Header,
    Login,
    ModTools,
    ItemsList,
    GameContent,
    BlackList,
    EventList
  },
  data() {
    return {
      rolledItems: 0,
      rarity: {
        Common: {
          color: "rgb(109 129 150)", //rgb(109 129 150); #34495e
          experience: 4,
          price: 8000,
        },
        Uncommon: {
          color: "#2980b9",
          experience: 8,
          price: 13000,
        },
        Rare: {
          color: "#e67e22",
          experience: 15,
          price: 30000,
        },
        Elite: {
          color: "#c0392b",
          experience: 20,
          price: 50000,
        },
        Epic: {
          color: "#8e44ad",
          experience: 25,
          price: 75000,
        },
        Legendary: {
          color: "#f1c40f",
          experience: 30,
          price: 100000,
        },
        Celestial: {
          color: "#00f6ff",
          experience: 50,
          price: 250000,
        },
        Exotic: {
          color: "#27ae60",
          experience: 75,
          price: 250000,
        },
      },
      items: [],
      newPlayer: true,
      development: false,
      keys: {
        bronze: {
          name: "bronze",
          price: 12000,
          requiredLevel: 0,
          requiredPrestigeLevel: 0,
          nickname: "Bronze Key",
          chances: [
            { name: "Common", chance: 65 },
            { name: "Uncommon", chance: 20 },
            { name: "Rare", chance: 14 },
            { name: "Elite", chance: 0.5 },
            { name: "Epic", chance: 0.4 },
            { name: "Legendary", chance: 0.05 },
            { name: "Celestial", chance: 0 },
            { name: "Exotic", chance: 0.05 },
          ]
        },
        silver: {
          name: "silver",
          price: 30000,
          requiredLevel: 20,
          requiredPrestigeLevel: 0,
          nickname: "Silver Key",
          chances: [
            { name: "Common", chance: 0 },
            { name: "Uncommon", chance: 65.05 },
            { name: "Rare", chance: 25 },
            { name: "Elite", chance: 9 },
            { name: "Epic", chance: 0.75 },
            { name: "Legendary", chance: 0.1 },
            { name: "Celestial", chance: 0.05 },
            { name: "Exotic", chance: 0.05 },
          ]
        },
        gold: {
          name: "gold",
          price: 100000,
          requiredLevel: 30,
          requiredPrestigeLevel: 0,
          nickname: "Gold Key",
          chances: [
            { name: "Common", chance: 0 },
            { name: "Uncommon", chance: 20 },
            { name: "Rare", chance: 60 },
            { name: "Elite", chance: 10 },
            { name: "Epic", chance: 5 },
            { name: "Legendary", chance: 4 },
            { name: "Celestial", chance: 0.5 },
            { name: "Exotic", chance: 0.5 },
          ]
        },
        platinum: {
          name: "platinum",
          price: 250000,
          requiredLevel: 100,
          requiredPrestigeLevel: 5,
          nickname: "Platinum Key",
          chances: [
            { name: "Common", chance: 5 },
            { name: "Uncommon", chance: 5 },
            { name: "Rare", chance: 30 },
            { name: "Elite", chance: 35 },
            { name: "Epic", chance: 20 },
            { name: "Legendary", chance: 3.6 },
            { name: "Celestial", chance: 0.7 },
            { name: "Exotic", chance: 0.7 },
          ]
        },
      },
      player: {},
      blackList: [],
      eventList: []
    };
  },
  methods: {
    fixPlayer(savedPlayer) {
      this.player = this.getNewPlayer();
      var aProps = Object.getOwnPropertyNames(this.player);
      var bProps = Object.getOwnPropertyNames(savedPlayer);
      for (var i = 0; i < aProps.length; i++) {
        var propNameA = aProps[i];
        var propNameB = bProps[i];
        if (propNameA == propNameB) {
          this.player[propNameA] = savedPlayer[propNameB];
          console.log("Yes: " + propNameA, propNameB);
        } else {
          console.log("No: " + propNameA, propNameB);
        }
      }
      this.savePlayer();
    },
    loadPlayer() {
      if (localStorage.getItem("player_account") == null) {
        console.log("New Player.");
        this.player = this.getNewPlayer();
      } else {
        this.newPlayer = false;
        console.log("Loaded save.");
        this.fixPlayer(JSON.parse(localStorage.getItem("player_account")));
      }
    },
    savePlayer() {
      console.info("Saved");
      localStorage.setItem("player_account", JSON.stringify(this.player));
    },
    deletePlayer() {
      var self = this;
      localStorage.removeItem("player_account");
      self.newPlayer = true;
      self.player = self.getNewPlayer();
      console.log(self.player);
    },
    getNewPlayer() {
      return {
        username: "Unknown",
        level: 1,
        gold: 240000,
        experience: 0,
        keys: {
          bronze: 0,
          silver: 0,
          gold: 0,
          platinum: 0
        },
        inventory: [],
        equipment: {
          armour: null,
          boots: null,
          helmet: null,
          weapon: null,
          greaves: null,
          amulet: null,
          special: null,
          shield: null,
          pet: null,
        },
        tools: {
          axe: null,
          shovel: null,
          pickaxe: null,
          fishing_rod: null,
        },
        statistics: {
          rarities_looted: [
            { name: "Common", count: 0 },
            { name: "Uncommon", count: 0 },
            { name: "Rare", count: 0 },
            { name: "Elite", count: 0 },
            { name: "Epic", count: 0 },
            { name: "Legendary", count: 0 },
            { name: "Celestial", count: 0 },
            { name: "Exotic", count: 0 },
          ],
          chests_opened: [
            { name: "bronze", count: 0},
            { name: "silver", count: 0},
            { name: "gold", count: 0},
            { name: "platinum", count: 0},
          ],
          found_keys: [
            { name: "bronze", count: 0},
            { name: "silver", count: 0},
            { name: "gold", count: 0},
            { name: "platinum", count: 0},
          ],
          items_looted: 0,
          items_sold: 0,
          currency: {
            earned_gold: 0,
            spent_gold: 0
          },
          total_experience: 0,
          total_levels: 1,
        },
        achievements: [],
        gameMode: false,
        newest_items: [],
        prestige_level: 1
      };
    },
    createPlayer(username, gameMode) {
      this.player.gameMode = gameMode;
      this.player.username = username;
      this.savePlayer();
      this.newPlayer = false;
    },
    getGold() {
      return this.player.gold;
    },
    setGold(gold) {
      return (this.player.gold = gold);
    },
    deductGold(amount) {
      this.player.statistics.currency.spent_gold += amount;
      this.player.gold -= amount;
    },
    buyKey(keyName, amount) {
      if (this.player.level < this.keys[keyName].requiredLevel)
        return console.log("Your level is not high enough.");
      var price = amount * this.keys[keyName].price;
      if (this.getGold() < price) return console.log("Not enough money.");
      this.addKeys(keyName, amount);
      this.deductGold(price);
      this.savePlayer();
      return console.log("Purchased for: " + price);
    },
    getPrestigeBonusGold(){
      if(this.player.prestige_level == 1) return 1;
      return this.player.prestige_level * 0.52;
    },
    getPrestigeBonusExp(){
      if(this.player.prestige_level == 1) return 1;
      return this.player.prestige_level * 0.52;
    },
    addKeys(keyName, amount) {
      console.log(keyName, amount);
      this.player.keys[keyName] += amount;
    },
    levelUp() {
      if (this.player.experience >= this.getRequiredExperience()) {
        this.player.experience = this.player.experience - this.getRequiredExperience();
        this.player.statistics.total_levels++;
        console.log("You have leveled up!");
        this.player.level++;
        this.savePlayer();
      }
    },
    getRequiredExperience() {
      return this.player.level * 50 + 50;
    },
    getExperiencePercentage() {
      return (
        (this.player.experience / this.getRequiredExperience()) * 100 + "%"
      );
    },
    getExperienceStatus() {
      return `${this.player.experience}/${this.getRequiredExperience()}`;
    },

    //prestige
    restartGameWithPrestige(){
      this.player.prestige_level++;
      this.player.gold = 240000;
      this.player.keys= {
          bronze: 0,
          silver: 0,
          gold: 0,
          platinum: 0
      },
      this.player.inventory = [];
      this.player.experience = 0;
      this.player.level = 1;
      this.player.newest_items = [];
    },
    prestigeLevelUp(){
      if(this.player.level < this.getPrestigeLevelRequired()) return console.log("Your level is not high enough.");
      this.restartGameWithPrestige();
    },

    getPrestigeLevelRequired(){
      return 20 + (this.player.prestige_level * 13);
    },

    //items
    loadItems(items) {
      this.items = items;
    },

    loadBlackList(items) {
      this.blackList = items;
    },

    loadEventList(items) {
      this.eventList = items;
    },

    //statistics

    addRarityToStatistics(rarityName){
      this.player.statistics.rarities_looted.filter(obj => {
        if(obj.name === rarityName){
            obj.count++
        }
      })
    },

    addChestToStatistics(keyName){
      this.player.statistics.chests_opened.filter(obj => {
        if(obj.name === keyName){
            obj.count++
        }
      })
    },
    
    addKeyToStatistics(keyName){
      this.player.statistics.found_keys.filter(obj => {
        if(obj.name === keyName){
            obj.count++
        }
      })
    },
    
    addItemsLootedToStatistics(amount){
      this.player.statistics.items_looted += amount;
    },

    addItemsSoldToStatistics(amount){
      this.player.statistics.items_sold += amount;
    },

    rollItems(keyName, amount){
      this.rolledItems = 0;
      console.clear()
      this.player.newest_items = [];
      if (this.player.keys[keyName] < amount) return console.log(`You do not have enough ${keyName} keys.`);
      this.addItemsLootedToStatistics(amount);
      this.player.keys[keyName] -= amount;
      for (let i = 0; i < amount; i++) {
        this.rollKey(keyName);
      }
      this.savePlayer();
      console.log(this.rolledItems);
    },

    rollItemsOld(keyName, amount) {
      this.player.newest_items = [];
      if (keyName == "bronze") {
        if (this.player.keys.bronze < amount)
          return console.log("You do not have enough Bronze keys.");
          this.addItemsLootedToStatistics(amount);
          this.player.keys.bronze -= amount;
          for (let i = 0; i < amount; i++) {
          this.rollBronzeKey();
        }
      }
      if (keyName == "silver") {
        if (this.player.keys.silver < amount)
          return console.log("You do not have enough Silver keys.");
          this.addItemsLootedToStatistics(amount);
          this.player.keys.silver -= amount;
          for (let i = 0; i < amount; i++) {
            this.rollSilverKey();
          }
      }
      if (keyName == "gold") {
        if (this.player.keys.gold < amount)
          return console.log("You do not have enough Gold keys.");
          this.addItemsLootedToStatistics(amount);
          this.player.keys.gold -= amount;
          for (let i = 0; i < amount; i++) {
            this.rollGoldKey();
          }
      }
      this.savePlayer();
    },



    rollKey(keyName){
      this.addChestToStatistics(keyName);
      var chance = Math.floor(Math.random() * 10000);
      var key = this.keys[keyName];
      var minChance = 0
      for (let i = 0; i < key.chances.length; i++) {
        var maxChance = minChance + (key.chances[i].chance * 100);
        console.log(`${minChance}, ${maxChance}, Rolled: ${chance}`);
        if (chance >= minChance && chance <= maxChance){
          return this.generateItem(key.chances[i].name);
        } 
        minChance += key.chances[i].chance * 100;
      }
      console.warn(`I have somehow finished: ${minChance}, ${maxChance}, Rolled: ${chance}`);
    },

    rollBronzeKey() {
      this.addChestToStatistics("bronze");
      var chance = Math.floor(Math.random() * 10000);
      if (chance <= 6500) return this.generateItem("Common");
      if (chance >= 6501 && chance <= 8500)
        return this.generateItem("Uncommon");
      if (chance >= 8501 && chance <= 9900) return this.generateItem("Rare");
      if (chance >= 9901 && chance <= 9950) return this.generateItem("Elite");
      if (chance >= 9951 && chance <= 9990) return this.generateItem("Epic");
      if (chance >= 9991 && chance <= 9995)
        return this.generateItem("Legendary");
      if (chance >= 9996 && chance <= 10000) return this.generateItem("Exotic");
    },

    rollSilverKey() {
      this.addChestToStatistics("silver");
      var chance = Math.floor(Math.random() * 10000);
      if (chance <= 6505) return this.generateItem("Uncommon");
      if (chance >= 6506 && chance <= 9005) return this.generateItem("Rare");
      if (chance >= 9006 && chance <= 9905) return this.generateItem("Elite");
      if (chance >= 9906 && chance <= 9980) return this.generateItem("Epic");
      if (chance >= 9981 && chance <= 9990)
        return this.generateItem("Legendary");
      if (chance >= 9991 && chance <= 9995)
        return this.generateItem("Celestial");
      if (chance >= 9996 && chance <= 10000) return this.generateItem("Exotic");
    },

    rollGoldKey() {
      this.addChestToStatistics("gold");
      var chance = Math.floor(Math.random() * 10000);
      if (chance <= 2000) return this.generateItem("Uncommon");
      if (chance >= 2001 && chance <= 8000) return this.generateItem("Rare");
      if (chance >= 8001 && chance <= 9000) return this.generateItem("Elite");
      if (chance >= 9001 && chance <= 9500) return this.generateItem("Epic");
      if (chance >= 9501 && chance <= 9900)
        return this.generateItem("Legendary");
      if (chance >= 9901 && chance <= 9950)
        return this.generateItem("Celestial");
      if (chance >= 9951 && chance <= 10000) return this.generateItem("Exotic");
    },

    //Item Rarities
    generateItem(rarityName) {
      this.rolledItems++;
      this.addRarityToStatistics(rarityName);
      var item = { Rarity: "" };
      do {
        item = this.items[Math.floor(Math.random() * this.items.length)];
        if (this.player.gameMode == true) {
          if (item.Level > this.player.level + 8) {
            item.Rarity = "";
            continue;
          }
        }
        if (item.Custom == 1) {
          item.Rarity = "";
          continue;
        }
        try {
          if (item.Name.toString().includes(":)")) {
            item.Rarity = "";
            continue;
          }
        } catch (error) {
          console.error(`${item.Name}: ${error}`);
          item.Rarity = "";
          continue;
        }
        if(item.Type == "Material"){
          item.Rarity = "";
          continue;
        }
        if(this.blackList.names.includes(item.Name)){
          item.Rarity = "";
          continue;
        }
        if(this.eventList.includes(item.ItemID)){
          item.Rarity = "";
          console.log("Event Item");
          continue;
        }
        if(this.blackList.IDs.includes(item.ItemID)){
          item.Rarity = "";
          continue;
        }
      } while (item.Rarity != rarityName);
      console.log(
        `%c Rolled ${item.Rarity}: [${item.ItemID}] ${item.Name} <${item.Custom}>`,
        `color: ${this.rarity[item.Rarity].color}`
      );
      this.addItemToInventory(item);
      this.getExperienceForRoll(item);
      return item;
    },
    addItemToInventory(item) {
      var id = item.ItemID;
      var index = this.player.inventory.findIndex((x) => x.ItemID === id);
      var unique_item = { ...item };
      unique_item.unique_id = (Math.random() * 1000) * 1000;
      this.player.newest_items.push(unique_item);
      if(this.isItemKey(id)) return console.log("It was a key!");
      if (index == -1) {
        item.collectedAmount = 1;
        item.locked = false;
        this.player.inventory.push(item);
      } else {
        this.player.inventory[index].collectedAmount++;
        console.warn(
          `Existing item in Inventory!! [${this.player.inventory[index].ItemID}] ${this.player.inventory[index].Name} (Amount: ${this.player.inventory[index].collectedAmount})`
        );
      }
    },
    isItemKey(ItemID){
      if(ItemID == 10000000001){
        //Plaatinum Key
        this.addKeyToStatistics("platinum")
        this.player.keys.platinum++;
        return true;
      }
      if(ItemID == 581){
        //Gold Key
        this.addKeyToStatistics("gold")
        this.player.keys.gold++;
        return true;
      }
      if(ItemID == 571){
        //Silver Key
        this.addKeyToStatistics("silver")
        this.player.keys.silver++;
        return true;
      }
      if(ItemID == 18){
        //Bronze Key
        this.addKeyToStatistics("bronze")
        this.player.keys.bronze++;
        return true;
      }

      

      
      

      return false;
    },


    //exp management
    getExperienceForRoll(item) {
      var multiply = 1;
      if (item.Level > 100) {
        multiply = item.Level / 100;
      }
      this.player.experience += Math.floor((this.rarity[item.Rarity].experience * multiply) * this.getPrestigeBonusExp());
      this.player.statistics.total_experience += Math.floor((this.rarity[item.Rarity].experience * multiply) * this.getPrestigeBonusExp());
      this.levelUp();
    },

    //inventory
    viewWholeInventory() {
      console.warn("Your Inventory:");
      for (var i = 0; i < this.player.inventory.length; i++) {
        const item = this.player.inventory[i];
        console.log(
          `%c [${item.ItemID}]: ${item.Name} Level: ${
            item.Level
          }, Price: ${this.getItemPrice(item)}, Rarity: ${item.Rarity}`,
          `color: ${this.rarity[item.Rarity].color}`
        );
      }
    },
    viewInventory(page) {
      var amount = 10;
      var currentPosition = page * amount;
      console.warn("Your Inventory page " + page + ":");
      for (var i = currentPosition; i < this.player.inventory.length; i++) {
        const item = this.player.inventory[i];
        console.log(
          `%c ${i}: [${item.ItemID}] ${item.Name} (${item.Rarity})`,
          `color: ${this.rarity[item.Rarity].color}`
        );
      }
    },

    sellInventory() {
      for (let i = 0; i < this.player.inventory.length; i++) {
        if(this.player.inventory[i].locked == true){
          console.log("Item is locked!");
          continue; 
        }
        this.sellItems(i, this.player.inventory[i].collectedAmount, "all");
        i--;
      }
      console.log("Sold Whole inventory");
      this.savePlayer();
    },

    //sell item
    sellItems(index, amount, type = "") {
      if (amount > this.player.inventory[index].collectedAmount) return console.log("You do not have enough of this item to sell it.");

      this.addItemsSoldToStatistics(amount);
      this.player.inventory[index].collectedAmount -= amount;
      this.player.gold += Math.floor((this.getItemPrice(this.player.inventory[index]) * amount) * this.getPrestigeBonusGold());
      this.player.statistics.currency.earned_gold += Math.floor((this.getItemPrice(this.player.inventory[index]) * amount) * this.getPrestigeBonusGold());
      if(this.player.inventory[index].collectedAmount == 0){  
        if (index > -1) this.player.inventory.splice(index, 1);
      } 
      if(type == "all") return;
      this.savePlayer();
    },


    //lock item
    lockItem(index){
      this.player.inventory[index].locked = true;
      this.savePlayer();
    },
    unlockItem(index){
      this.player.inventory[index].locked = false;
      this.savePlayer();
    },

    getItemPrice(item) {
      var multiply = 1;
      if (item.Level > 100) {
        multiply = item.Level / 100;
      }
      return this.rarity[item.Rarity].price * multiply;
    },

    //mod Tools
    playerAddLevel() {
      this.player.experience = this.getRequiredExperience();
      this.levelUp();
    },
    playerAddPrestige() {
      this.player.prestige_level++;
      this.savePlayer();
    },
    playerAddGold() {
      this.player.gold += 50000;
      this.savePlayer();
    },
  },
  created: function () {
    this.loadPlayer();
    console.log(this.player.keys);
  }
}
</script>

<style scoped>
.container {
  height: 100%;
}

#gameWindow {
  height: 100%;
}
</style>