<template>
    <div class="container">
        <BackButton pageTitle="Character" @goBack="goBack" />
        <div class="content">
            <div id="character" class="flex flex-col">
                <div class="title">
                    Rarities looted:
                </div>
                <div class="flex flex-col">
                    <div v-for="item in statistics.rarities_looted" :key="(Math.random() * 10) + item.name" class="">{{ item.name }}: {{ item.count }}</div>
                </div>
                <div class="title">
                    Chests opened:
                </div>
                <div class="flex flex-col">
                    <div v-for="item in statistics.chests_opened" :key="(Math.random() * 10) + item.name" class="">{{ item.name }}: {{ item.count }}</div>
                </div>

                <div class="title">
                    Keys looted:
                </div>
                <div class="flex flex-col">
                    <div v-for="item in statistics.found_keys" :key="(Math.random() * 10) + item.name" class="">{{ item.name }}: {{ item.count }}</div>
                </div>
                <div class="title">
                    Items sold: {{ statistics.items_sold }}
                </div>
                <div class="title">
                    Items looted: {{ statistics.items_looted }}
                </div>
                <div class="title">
                    Prestige Level: {{ this.player.prestige_level }}
                </div>
                <div class="title">
                    Total Levels: {{ statistics.total_levels }}
                </div>
                <div class="title">
                    Total Experience: {{ statistics.total_experience }}
                </div>
                <div class="title">
                    Gold earned: {{ statistics.currency.earned_gold }}
                </div>
                <div class="title">
                    Gold spent: {{ statistics.currency.spent_gold }}
                </div>
                <div class="title">
                    
                </div>
                <button @click="prestigeLevelUp" :disabled="this.player.level < prestigeRequiredLevel">Restart With Prestige</button>
                <span>You have to be level {{ prestigeRequiredLevel }} or more to prestige.</span>

                <button @click="deletePlayer">Delete Account</button>
            </div>
        </div>
    </div>
</template>

<script>

    import BackButton from "./BackButton.vue";


    export default {
        name: "Character",
        components: {
            BackButton
        },
        props: ["player", "prestigeRequiredLevel"],
        data(){
            return {
                statistics: {}
            }
        },
        methods:{
            goBack(){
                this.$emit('goBack')
            },
            prestigeLevelUp(){
                this.$emit('prestigeLevelUp');
            },
            deletePlayer(){
                this.$emit('deletePlayer');
            },
        },
        created(){
            this.statistics = this.player.statistics;
            console.log(this.statistics);
            console.log(this.prestigeRequiredLevel);
        }
    }
</script>

<style scoped>
.flex{
    display: flex;
}
.flex-col{
    flex-direction: column;
}
.container{
    height: 100%;
}
.content{
    height: calc(100% - 3rem);
    width: 100%;
    padding: 1rem;
    color: var(--text);
    font-size: 0.9rem;
    overflow-y: auto;
}
span{
    text-align: center;
    color: yellow;
}

#character{
    
}
.title{
    font-weight: 500;
    margin-top: 1rem;
    font-size: 1rem;
}

button{
    width: 12rem;
    padding: 1rem;
    border-radius: 0.5rem;
    margin-top: 1rem;
}

button{
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 0.5rem;
    padding: 0.5rem;
    border-radius: 0.2rem;
    align-self: center;
    font-weight: 500;
}

button svg{
    height: 100%;
}

button:hover{
    background: rgb(202, 202, 202); 
}
</style>