<template>
    <div></div>
</template>

<script>

    import Data from '../assets/data/items.json';
    import Customs from '../assets/data/customItemsList.json';

    export default {
        name:"ItemsList",
        props: ["items"],
        data(){
            return {
                database: [],
            }
        },
        methods:{
            getItemsList(){
                this.database = Data.concat(Customs);
                return this.$emit('loadItems', this.database)
            }
        },
        created(){
            console.log(this.Customs);
            this.getItemsList()
        }
    }
</script>

