<template>
    <div class="container">
        <BackButton pageTitle="Inventory" @goBack="goBack" />
        <div class="itemInfo">
          <ItemInfo @closeItemInfo="closeItemInfo()" :rarity="rarity" v-if="showItem" :item="item"  />
        </div>
        <div class="content">
            <div class="btnContainer flex">
                    <button v-if="player.inventory.length > 0" @click="sellInventory" class="self-center">Sell all</button>
                    <div class="self-center">
                        <button style="display: none;" v-if="player.inventory.length > 0" @click="showDropDown" class="self-center">Filter</button>
                        <InventoryFilter @filterContent="filterContent($event[0], $event[1])"  />
                    </div>
                </div>
            <div class="listOfItems">
                <div v-for="item in inventory_content" :key="item.unique_id" class="item">
                    <div class="self-center" :style="{color: rarity[item.Rarity].color }" @click="itemInfo(item)">{{ item.collectedAmount }}x {{ item.Name }}</div>
                    <div v-if="item.locked == true" class="lockStatus">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fill-rule="evenodd" d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z" clip-rule="evenodd" />
                        </svg>
                    </div>
                    <div class="ml-auto flex actionButtons">
                        <button @click="lockItem(item.ItemID)"  v-if="item.locked == false" class="">
                            <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                <path fill-rule="evenodd" d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z" clip-rule="evenodd" />
                            </svg>
                        </button>
                        <button @click="unlockItem(item.ItemID)" v-if="item.locked == true" class="">
                            <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                <path d="M10 2a5 5 0 00-5 5v2a2 2 0 00-2 2v5a2 2 0 002 2h10a2 2 0 002-2v-5a2 2 0 00-2-2H7V7a3 3 0 015.905-.75 1 1 0 001.937-.5A5.002 5.002 0 0010 2z" />
                            </svg>
                        </button>
                        <button :disabled="item.locked == true" @click="sellItems(item.ItemID, 1)" class="">Sell</button>
                    </div>
                </div>
            </div>
            <div class="flex footer" v-if="lastPage != 0">
                <div class="self-center button">
                    <button v-if="page != 0" @click="getPreviousPage" >
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fill-rule="evenodd" d="M9.707 16.707a1 1 0 01-1.414 0l-6-6a1 1 0 010-1.414l6-6a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l4.293 4.293a1 1 0 010 1.414z" clip-rule="evenodd" />
                        </svg>
                    </button>
                </div>
                <div class="ml-auto self-center pagination">{{ page + 1 }} / {{ lastPage + 1 }}</div>
                <div class="ml-auto self-center button">
                    <button v-if="page != lastPage" @click="getNextPage">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fill-rule="evenodd" d="M10.293 3.293a1 1 0 011.414 0l6 6a1 1 0 010 1.414l-6 6a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-4.293-4.293a1 1 0 010-1.414z" clip-rule="evenodd" />
                        </svg>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import BackButton from "./BackButton.vue";
    import ItemInfo from "./ItemInfo.vue";
    import InventoryFilter from "./InventoryFilter.vue";

    export default {
        name: "Inventory",
        components: {
            BackButton,
            ItemInfo,InventoryFilter
        },
        data(){
            return {
                inventory_content: [],
                filtered_inventory: [],
                filterType: [],
                filterRarity: [],
                page: 0,
                lastPage: 0,
                item: {},
                showItem: false,
                itemsPerPage: 50
            }
        },
        props: ["items", "rarity", "player"],
        methods:{
            filterByType(type){
                this.filterType = type;
            },
            goBack(){
                this.$emit('goBack')
            },
            getPages(length){
                var number = length / this.itemsPerPage;
                if(number % 1 != 0){
                    number =  Math.floor(number)
                }
                else{
                    if(number != 0)  number--;
                }
                this.lastPage = number;
                if(this.page > this.lastPage) this.page = 0;
            },
            filterContent(type, rarity){
                this.filterType = type;
                this.filterRarity = rarity;
            },
            getPageContent(){
                var arrayOfItems  = [];
                var filteredInventory = [];
                if(this.filterType.length > 0){
                    console.log("Type != all")
                    for (let i = 0; i < this.filterType.length; i++) {
                        let array = [...this.player.inventory].filter(obj => {
                            return obj.Type === this.filterType[i]
                        })
                        if(i == 0) filteredInventory = [...array];
                        if(i != 0) filteredInventory = [...filteredInventory].concat(array)
                    }
                    arrayOfItems = [...filteredInventory];

                    console.log("After Type: ");
                    console.log(filteredInventory);
                    if(this.filterRarity.length > 0){
                        console.log("Rarity != all")
                        for (let i = 0; i < this.filterRarity.length; i++) {
                            let array = [...arrayOfItems].filter(obj => {
                                return obj.Rarity === this.filterRarity[i]
                            })
                            if(i == 0) filteredInventory = [...array];
                            if(i != 0) filteredInventory = [...filteredInventory].concat(array)
                        }
                        console.log("After Rarity: ");
                        console.log(filteredInventory);
                    }
                }
                else{
                    console.log("Type == all")
                    if(this.filterRarity.length > 0){
                        console.log("Rarity != all")
                        for (let i = 0; i < this.filterRarity.length; i++) {
                            let array = [...this.player.inventory].filter(obj => {
                                return obj.Rarity === this.filterRarity[i]
                            })
                            if(i == 0) filteredInventory = [...array];
                            if(i != 0) filteredInventory = [...filteredInventory].concat(array)
                        }
                        
                    }
                    else{
                        console.log("Rarity == all")
                        filteredInventory =  [...this.player.inventory];
                    }
                }
                console.log("Last Inventory: ");
                console.log(filteredInventory);
                this.getPages(filteredInventory.length);
                this.inventory_content = []
                var from = this.page * this.itemsPerPage;
                var to = from + this.itemsPerPage;
                var limit = filteredInventory.length;
                if(to > limit) to = limit;
                for (let i = from; i < to; i++) {
                    const element = filteredInventory[i];
                    this.inventory_content.push(element)
                }
                this.showItem = false;
            },
            getNextPage(){
                if(this.page == this.lastPage){
                    return console.log("Last page!");
                }
                this.page++;
                this.getPageContent()
            },
            getPreviousPage(){
                if(this.page == 0){
                    return console.log("First page!");
                }
                this.page--;
                this.getPageContent()
            },
            itemInfo(item){
                if(this.item == item){
                    this.item = {}
                    return this.showItem = false;
                }
                this.item = item;
                this.showItem = true;
            },
            getIndex(ItemID){
                var index = this.player.inventory.findIndex((x) => x.ItemID === ItemID);
                if(index > -1) return index;
            },
            removeItemOnScreen(ItemID){
                var index = this.inventory_content.findIndex((x) => x.ItemID === ItemID);
                if(index > -1) this.inventory_content.splice(index, 1);
                return this.getPageContent();
            },
            lockItem(ItemID) {
                this.$emit("lockItem", this.getIndex(ItemID));
            },
            unlockItem(ItemID) {
                this.$emit("unlockItem", this.getIndex(ItemID));
            },
            sellItems(ItemID, amount) {
                this.$emit("sellItems", [this.getIndex(ItemID), amount]);
                this.removeItemOnScreen(ItemID);
            },
            sellInventory(){
                this.$emit('sellInventory')
                this.page = 0;
                this.lastPage = 0;
                this.getPageContent();
            },
            closeItemInfo(){
                this.showItem = false;
            }
        },
        created(){
            this.getPageContent()
        }
    }
</script>

<style scoped>
.footer{
    margin-top: 1rem;
}
.actionButtons{
    gap: 0.2rem;
}
.btnContainer{
    height: 3rem;
    gap: 0.2rem;
}
.btnContainer button{
    width: 5rem;
    justify-content: center;
    font-weight: 600;
}

.actionButtons button{
    display: flex;
    width:  3rem;
    justify-content: center;
}
.itemInfo{
    width: 100%;
    position: fixed;
    bottom: 7rem;
    left: 0;
}
.item_info{
    width: 80%;
    margin: 0 auto;
}
.electron .listOfItems{
    height: calc(100vh - 16rem);
    overflow-y: auto;
}

.web .listOfItems{
    height: calc(100vh - 14rem);
    overflow-y: auto;
}
.container{
    height: 100%;
}
.content{
    height: calc(100% - 3rem);
    width: 100%;
}
.item{
    padding: 0.5rem;
    border-bottom: 1px solid gray;
    display: flex;
}
.flex{
    display: flex;
}
.self-center{
    align-self: center;
}
.pagination{
    color: var(--text);
}
.ml-auto{
    margin-left: auto;
}
button{
    cursor: pointer;
    outline: none;
    border: none;
    padding: 0.5rem;
    border-radius: 0.2rem;
    align-self: center;
    font-weight: 600;
    width: 100%;
    height: 2rem;
    display: flex;
}

.button{
    width: 2rem;
}

button svg{
    align-self: center;
    height: 1rem;
}

.lockStatus{
    margin-left: 0.2rem;
}

.lockStatus svg{
    height: 1rem;
    color: var(--text);
}

button:hover{
    background: rgb(202, 202, 202); 
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

</style>